.header {
  background: #1F2128;
  box-shadow: inset 0px 0px 30px 0px rgba(255, 255, 255, 0.25);
  border-bottom: 1px solid #80FF00;
  padding: 22px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &__logo {
    width: 197px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
    li a {
      font-family: 'CenturyGothic';
      font-size: 17px;
      line-height: 120%;
      font-weight: 700;
      &:hover {
        color: #80FF00;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 67px;
  height: 45px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 8px;
    background: #80FF00;
    border-radius: 1px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 18px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 18px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__list {
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 20px 0;
    &__logo {
      width: 139px;
    }
    &__list {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 65%;
      height: 100%;
      flex-wrap: nowrap;
      flex-direction: column;
      align-items: start;
      gap: 40px;
      background: rgb(18, 19, 23);
      box-shadow: 0px 0px 40px 0px rgba(255, 255, 255, 0.25);
      padding: 130px 25px 50px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      li a {
        font-size: 35px;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__list {
      gap: 25px;
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 15px 0;
    &__logo {
      width: 73px;
    }
    &__list {
      width: 77%;
      padding: 80px 25px 30px;
    }
  }
  .burger {
    width: 26px;
    height: 17px;
    span {
      height: 3px;
    }
    &--active {
      span:nth-child(1) {
        top: 7px;
      }
      span:nth-child(3) {
        bottom: 7px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__list {
      width: 100%;
    }
  }
}
