.btn-green {
  background: #80FF00;
  box-shadow: 0px 0px 15px 0px rgb(3, 242, 149);
  border: 4px solid rgb(158, 255, 169);
  border-radius: 25px;
  &:hover {
    box-shadow: 0px 0px 30px 0px rgb(3, 242, 149);
  }
  span {
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #0B3500;
  }
}