.main-section {
  position: relative;
  background-image: url("../img/main-img.webp");
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;
  padding: 210px 0 270px;
  &__title {
    font-size: 30px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__subtitle {
    font-family: 'DMSans';
    font-size: 70px;
    line-height: 100%;
    font-weight: 700;
    letter-spacing: 4.2px;
    text-transform: uppercase;
    color: #80FF00;
    margin-top: 70px;
  }
  &__btn {
    padding: 28px 64px;
    margin-top: 70px;
    span {
      font-size: 45px;
    }
  }
}
.info {
  position: relative;
  z-index: 2;
  padding-bottom: 95px;
  margin-top: -88px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 80px;
  }
  &__item {
    &-title {
      font-family: 'CenturyGothic';
      font-size: 48px;
      line-height: 120%;
      font-weight: 700;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 30px;
      p {
        font-size: 27px;
        line-height: 120%;
        font-weight: 500;
      }
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 30px;
      margin-top: 30px;
      li {
        position: relative;
        font-size: 27px;
        line-height: 120%;
        font-weight: 500;
        padding-left: 50px;
        &:after {
          content: '';
          position: absolute;
          top: 4px;
          left: 0;
          width: 20px;
          height: 24px;
          background-image: url("../img/list-icon.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        span {
          display: block;
          font-weight: 700;
          color: #80FF00;
        }
      }
    }
  }
}
.banner {
  background-image: url("../img/banner-img.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 3px solid #80FF00;
  border-radius: 50px;
  padding: 120px 94px;
  margin: 80px 0;
  &__desc {
    max-width: 825px;
  }
  &__title {
    font-size: 30px;
    line-height: 120%;
    color: #fff;
  }
  &__subtitle {
    font-family: 'DMSans';
    font-size: 65px;
    line-height: 120%;
    font-weight: 700;
    color: #fff;
    margin-top: 40px;
  }
  &__btn {
    padding: 27px 47px;
    margin-top: 40px;
    span {
      font-size: 36px;
    }
  }
}
.banner2 {
  background-image: url("../img/banner-img2.webp");
}

@media screen and (max-width: 1600px) {
  .info {
    &__items {
      gap: 50px;
    }
    &__item {
      &-title {
        font-size: 30px;
      }
      &-text {
        margin-top: 25px;
        p {
          font-size: 20px;
        }
      }
      &-list {
        gap: 25px;
        margin-top: 25px;
        li {
          font-size: 20px;
          &:after {
            width: 14px;
            height: 16px;
          }
        }
      }
    }
  }
  .banner {
    margin: 50px 0;
  }
}

@media screen and (max-width: 1400px) {
  .main-section {
    padding: 100px 0 140px;
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 40px;
      margin-top: 20px;
    }
    &__btn {
      border-radius: 13px !important;
      padding: 12px 34px;
      margin-top: 20px;
      span {
        font-size: 20px;
      }
    }
  }
  .info {
    margin-top: -50px;
  }
  .banner {
    padding: 50px 40px;
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 40px;
      margin-top: 20px;
    }
    &__btn {
      border-radius: 10px !important;
      padding: 10px 20px;
      margin-top: 20px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main-section {
    &__title {
      font-size: 15px;
    }
    &__subtitle {
      font-size: 33px;
    }
  }
  .info {
    padding-bottom: 40px;
  }
  .banner {
    border-radius: 23px;
    &__title {
      font-size: 15px;
    }
    &__subtitle {
      font-size: 33px;
    }
  }
}

@media screen and (max-width: 768px) {
  .main-section {
    background: transparent;
    padding: 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 120px);
      background-image: url("../img/main-img.webp");
      background-repeat: no-repeat;
      background-position: bottom center;
      background-size: cover;
    }
    &__inner {
      position: relative;
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 250px 0 25px;
    }
  }
  .info {
    margin-top: 0;
  }
  .banner {
    max-width: 500px;
    background-image: url("../img/banner-img__mob.webp");
    padding: 300px 20px 20px;
    margin: 40px auto;
    &__desc {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
  .banner2 {
    background-image: url("../img/banner-img__mob2.webp");
  }
}

@media screen and (max-width: 576px) {
  .main-section {
    &:after {
      height: calc(100% - 20px);
      background-image: url("../img/main-img2.webp");
    }
    &__title {
      font-size: 10px;
    }
    &__subtitle {
      font-size: 20px;
      margin-top: 12px;
    }
    &__btn {
      border-radius: 10px !important;
      padding: 6px 19px;
      margin-top: 12px;
      span {
        font-size: 12px;
      }
    }
  }
  .info {
    &__items {
      gap: 20px;
    }
    &__item {
      &-title {
        font-size: 16px;
      }
      &-text {
        gap: 10px;
        margin-top: 10px;
        p {
          font-size: 12px;
        }
      }
      &-list {
        gap: 10px;
        margin-top: 10px;
        li {
          font-size: 12px;
          padding-left: 24px;
          &:after {
            top: 2px;
            width: 8.7px;
            height: 10px;
          }
        }
      }
    }
  }
  .banner {
    &__title {
      font-size: 12px;
    }
    &__subtitle {
      font-size: 20px;
      margin-top: 12px;
    }
    &__btn {
      border-radius: 10px !important;
      padding: 8px 21px;
      margin-top: 12px;
      span {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .main-section {
    &:after {
      height: calc(100% - 80px);
    }
  }
}